.projects-container {
  display: flex;
  justify-content: space-between;
  margin-top: 5rem;
  gap: 1rem;
  justify-content: center;
  flex-flow: row wrap;
}
.projects-container-btn {
  display: flex;
  margin-top: 4rem;
  justify-content: center;
}
@media (max-width: 660px) {
  .projects-container-btn {
    margin-top: 0;
  }
}
.projects-container-btn button {
  width: 9rem;
  color: var(--text-color);
  padding: 0.4rem 0.9rem;
  background: var(--gradient);
  text-decoration: none;
  border-radius: 0.4rem;
  -webkit-appearance: auto;
  -moz-appearance: auto;
  appearance: auto;
  border-style: none;
} /*# sourceMappingURL=projectsContainer.css.map */
