@media (max-width: 1204px) {
  .home-container h1 {
    font-size: 4rem;
  }
}
@media (max-width: 1095px) {
  .home-container h1 {
    font-size: 3.4rem;
    text-align: center;
  }
}
@media (max-width: 964px) {
  .home-container h1 {
    font-size: 3rem;
  }
}
@media (max-width: 878px) {
  .home-container h1 {
    font-size: 2.7rem;
  }
}
@media (max-width: 804px) {
  .home-container h1 {
    font-size: 2.6rem;
  }
}

.home-container span {
  font-size: 2.5rem;
  margin-right: 0.1rem;
  background: var(--gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.home-container p {
  text-align: center;
  padding: 0;
  /* display: inline-block; */
}
.home-container p:nth-child(3) {
  font-size: 1.6rem;
  margin: 0;
  text-align: center;
}
@media (max-width: 1095px) {
  .home-container p:nth-child(3) {
    font-size: 1.3rem;
  }
}
@media (max-width: 964px) {
  .home-container p:nth-child(3) {
    font-size: 1.2rem;
  }
}

.home-container p:nth-child(1) {
  font-size: 2.5rem;
  margin-top: 5rem;
  margin-bottom: 0;
  text-align: left;
}
@media (max-width: 1204px) {
  .home-container p:nth-child(1) {
    font-size: 3.5rem;
  }
}
@media (max-width: 1095px) {
  .home-container p:nth-child(1) {
    font-size: 2.5rem;
  }
}
@media (max-width: 878px) {
  .home-container p:nth-child(1) {
    font-size: 2.2rem;
  }
}
@media (max-width: 804px) {
  .home-container p:nth-child(1) {
    font-size: 1.9rem;
  }
} /*# sourceMappingURL=home.css.map */
